import React from "react";

import { Link } from "react-router-dom";
import {
  NavbarSection,
  Logo,
  LogoText,
  LogoList,
  ListItem,
  Anchor,
  Inputt,
  Labell,
} from "./style";

function Navebar() {
  return (
    <NavbarSection>
      <div className="container">
        <Logo>
          <LogoText>Biko Profile</LogoText>
        </Logo>
        <Inputt type="checkbox" id="nav" />
        <Labell htmlFor="nav"></Labell>

        <LogoList>
          <ListItem>
            <Anchor to="/">Home</Anchor>
          </ListItem>
          <ListItem>
            <Anchor to="/works">Works</Anchor>
          </ListItem>
          <ListItem>
            <Anchor to="/portfolio">Portfolio</Anchor>
          </ListItem>

          <ListItem>
            <Anchor to="/about">About</Anchor>
          </ListItem>
          <ListItem>
            <Anchor to="/contact">Contact</Anchor>
          </ListItem>
        </LogoList>
      </div>
    </NavbarSection>
  );
}

export default Navebar;
